.App {
  text-align: center;
  color: rgb(14, 55, 70);
  text-transform: uppercase;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

table{
  border: 1px solid black;
}

.t-row{
  height: 35px;
}

.t-name{
  width: 400px;
}

.t-code{
  width:100px;
  text-align: center;
}
.t-ed{
  width: 80px;
  text-align: center;
}

.t-del{
  width: 80px;
  text-align: center;
}

.notification{
  position: absolute;
  bottom: 4px;
  right: 50%;
  /* left: ; */
  width: max-content;
  background-color: #313e2c;
  color: #aaec8a;
  box-shadow: 0 1px 10px rgba(0,0,0,0.1);
  transform: translateY(30px);
  opacity: 0;
  visibility: hidden;
  animation: fade-in 3s linear;
}

.rnotification{
  position: absolute;
  bottom: 4px;
  right: 50%;
  /* left: ; */
  width: max-content;
  background-color: #f77272;
  color: #160101;
  box-shadow: 0 1px 10px rgba(0,0,0,0.1);
  transform: translateY(30px);
  opacity: 0;
  visibility: hidden;
  animation: fade-in 3s linear;

}


.not_body{
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 16px 8px;
}
.not_progress{
  position: absolute;
  left: 4px;
  bottom: 2px;
  width: calc(100% - 8px);
  height: 3px;
  transform: scaleX(0);
  background: linear-gradient(to right, #313e2c, #aaec8a );
  border-radius: inherit;
  animation: progress 2.5s 0.3s linear;
}
.rnot_progress{
  position: absolute;
  left: 4px;
  bottom: 4px;
  width: calc(100% - 8px);
  height: 3px;
  transform: scaleX(0);
  background: linear-gradient(to right,#fd9292, #160101);
  border-radius: inherit;
  animation: progress 2.5s 0.3s linear;
}


@keyframes fade-in {
  5%{
    opacity: 1;
    visibility: visible;
    transform: translateY(0);
  }
  95%{
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes progress{
  to {
    transform: scaleX(1);
  }
}